<template>
    <div>
        <form class="mui-input-group complainWrap">
            <div class="mui-input-row">
                <label><span>*</span></label>
                <input type="text" class="mui-input-clear" v-model='complainType' placeholder="">
                <span @click='showPicker' class="mui-icon mui-icon-arrowright"></span>
            </div>
            <mt-picker :slots="slots" @change="onValuesChange" v-show='isShow'></mt-picker>
            <div class="mui-input-row tit">
                <label><span>*</span></label>
                <input type="text" v-model='complainTitle' class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label><span class="tit"></span></label>
                <input v-model='complainCompany' type="text" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label><span class="tit"></span></label>
                <input v-model='complainCar' type="text" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label><span class="tit"></span></label>
                <input v-model='complainPerson' type="text" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label><span class="tit"></span></label>
                <input v-model='complainTel' type="number" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label><span>*</span></label>
                <input type="text" v-model='complainContent' class="mui-input-clear" placeholder="">
            </div>
        </form>
        <button @click='submit($event)' type="button" data-loading-text='...' data-loading-icon="mui-spinner mui-spinner-custom" class="submitComplain mui-btn mui-btn-primary"></button>
    </div>
</template>

<script>
import mui from "../lib/mui/js/mui.min.js"
import { Toast, Picker } from 'mint-ui'
export default {
    data(){
        return{
            isShow: false,
            slots: [
                {
                flex: 1,
                values: ['', '', '', ''],
                className: 'slot1',
                textAlign: 'center'
                }
            ],
            complainType: '',
            complainTitle: '',
            complainContent: '',
            complainCompany: '',
            complainCar: '',
            complainPerson: '',
            complainTel: ''
        }
    },
    methods: {
        showPicker(){
            this.isShow=!this.isShow
            if(this.isShow){
                this.complainType=picker.getValues()[0]
            }
        },
        onValuesChange(picker, values){
            this.complainType=picker.getValues()[0]
        },
        chooseType(e){
            this.isShow = !this.isShow;
            this.complainType=e.target.innerText
        },
        submit(e) {   //
            if(!this.complainType.trim()){
                Toast('')
                return
            }else if (!this.complainTitle.trim()){
                Toast('')
                return
            }else if(!this.complainContent.trim()) {
                 Toast('')
                 return
            }
            this.complainType=''
            this.complainTitle=''
            this.complainContent=''
            this.complainCompany=''
            this.complainCar=''
            this.complainPerson=''
            this.complainTel=''
            mui(e.target).button('loading')
            setTimeout(function(){
                mui(e.target).button('reset')
                Toast('')
            },2000)
        }
    }
}
</script>

<style lang='scss'>
    body {
        background-color: #f8f8f8;
    }
    .complainWrap {
        margin-top: 10px;
        .tit {
            border-top: 1px solid #eee;
        }
        .mui-input-row {
            height: 50px;
            border-bottom: 1px solid #eee;
            position: relative;
            .mui-icon-clear {
                top: 33px;
            }
            label {
                line-height: 50px;
                padding: 0 15px;
                color: #333;
                font-size: 15px;
                span {
                    color: red;
                    margin-right: 3px;
                    border-top: none;
                }
                .tit {
                    color: #333;
                    margin-left: 8px;
                }
            }
            input {
                margin-top: 5px;
                font-size: 15px;
            }
            >span {
                width: 24px;
                height: 24px;
                position: absolute;
                top: 50%;
                right: 10px;
                transform: translateY(-50%);
                color: #ddd;
            }
        }
        .picker 
        .picker-item{
            font-size: 15px;
        }
        .mui-input-row:after {
            height: 0 ;
        }
        .mui-input-row:last-child {
            border: none;
        }
        .mui-table-view::before {
            height: 0;
        }
    }
    .complainWrap:before, .complainWrap:after{
        height: 0;
    }
    .submitComplain {
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 40px;
        background-color: #9fc7fa;
        border: none;
        font-size: 16px;
    }
</style>



