<template>
<div class="infoWrap">
  <mt-navbar v-model="selected" :fixed="true">
    <mt-tab-item id="1"></mt-tab-item>
    <mt-tab-item id="2"></mt-tab-item>
    <mt-tab-item id="3"></mt-tab-item>
    <mt-tab-item id="4"></mt-tab-item>
    <mt-tab-item id="5"></mt-tab-item>
  </mt-navbar>

  <!-- tab-container -->
  <mt-tab-container v-model="selected">
    <mt-tab-container-item id="1">
      <div @click="goOut" class="msg">
        <p></p>
        <div>
          <img src="../assets/img/other/info2.png"/>
        </div>
        <span>-2017.10.23</span>
      </div>
      <div @click="goOut" class="msg">
        <p></p>
        <div>
          <img src="../assets/img/other/info3.jpg">
        </div>
        <span>-2017.10.23</span>
      </div>
      <div @click="goOut" class="msg">
        <p></p>
        <div>
          <img src="../assets/img/other/info2.png"/>
        </div>
        <span>-2017.10.23</span>
      </div>
      <div @click="goOut" class="msg">
        <p></p>
        <div>
          <img src="../assets/img/other/info3.jpg">
        </div>
        <span>-2017.10.23</span>
      </div>
    </mt-tab-container-item>
    <mt-tab-container-item id="2">
      <div @click="goOut" class="msg">
        <p></p>
        <div>
          <img src="../assets/img/other/info2.png"/>
        </div>
        <span>-2017.10.23</span>
      </div>
    </mt-tab-container-item>
    <mt-tab-container-item id="3">

    </mt-tab-container-item>
    <mt-tab-container-item id="4">

    </mt-tab-container-item>
    <mt-tab-container-item id="5">
      <div class="msg">
        <p></p>
        <div>
          <img src="../assets/img/other/info3.jpg">
        </div>
        <span>-2017.10.23</span>
      </div>
    </mt-tab-container-item>
  </mt-tab-container>
</div>
</template>

<script>
  import { Navbar, TabItem, Cell} from 'mint-ui';
  import { TabContainer, TabContainerItem } from 'mint-ui';

export default {
  name: 'info',
  data () {
    return {
      selected:"1"
    }
  },
  methods:{
    goOut(){
      window.location.href= 'http://static.shanghaiqixiu.org/article/1.html'
    }
  },
  components:{
    mtCell: Cell,
    mtNavbar: Navbar,
    mtTabItem: TabItem,
    mtTabContainer: TabContainer,
    mtTabContainerItem:TabContainerItem
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .infoWrap{
    margin-top: 50px;
    .mint-navbar {
      height: 47px;
    }
    a {
      .mint-tab-item-label {
        color: #666;
      } 
    }
    .is-selected {
      border-bottom: 2px solid #26a2ff !important;
    }
  }
  .msg{
    padding: 10px;
    border-bottom: 1px solid #f7f7f7;
    height: 130px;
    font-size: 14px;
    position: relative;
    p{
      margin-right: 105px;
      color: #333;
    }
    div{
      width: 105px;
      height: 105px;
      overflow: hidden;
      position: absolute;
      top:10px;
      right: 10px;
      border-radius: 10px;
      img{
        height: 100%;
        width: auto;
      }
    }
    span{
      display: block;
      font-size: 12px;
      color: #8f8f8f;
      position: absolute;
      bottom: 10px;
    }
  }
</style>




